.elements {
  list-style: none;
  padding: 50px 0 67px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(282px, 1fr));
  row-gap: 20px;
  column-gap: 17px;
}

@media screen and (max-width: 425px) {
  .elements {
    width: 88%;
    padding: 36px 0 48px;
  }
}